import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { PhysicsGCSEComponent } from './physics-gcse/physics-gcse.component';
import { ChemistryGcseComponent } from './chemistry-gcse/chemistry-gcse.component';
import { UsersDataControlComponent } from './users-data-control/users-data-control.component';



const routes: Routes = [
  {path:"login",component:LoginComponent},
  {path:"dashboard",component:DashboardComponent},
  {path:"physicsGCSE",component:PhysicsGCSEComponent},
  {path:"chemistryGCSE",component:ChemistryGcseComponent},
  {path:"usersDataControl",component:UsersDataControlComponent},
  {path:"", redirectTo:"/dashboard",pathMatch:"full"}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
