import { Component, OnInit } from '@angular/core';
import { SessionService } from '../session.service';
import { Router } from '@angular/router';
import {HttpClient} from '@angular/common/http'

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username="";
  password="";
  constructor(private sessionS:SessionService,private router:Router,private http:HttpClient) {}

  ngOnInit() {
    var loggedIn=this.sessionS.getUser();
    if(loggedIn && loggedIn.token){
      this.router.navigateByUrl("/dashboard")
    }
  }

  login(){
    this.http.post("/auth/login",{username:this.username,password:this.password}).subscribe(
      (data)=>{
        console.log(data)
        this.sessionS.setUser(data);
        window.location.href=""
      },
      (error)=>{

      }
    )
  }
}
