import { Component, OnInit } from '@angular/core';
import { SessionService } from '../session.service';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-physics-gcse',
  templateUrl: './physics-gcse.component.html',
  styleUrls: ['./physics-gcse.component.css']
})
export class PhysicsGCSEComponent implements OnInit {

  constructor(private sessionS:SessionService, private http:HttpClient,private router:Router) { }
  Hide=true;
  showTable=false;
  physArray: any
  whoIs=''
  ngOnInit() {
    var userData=this.sessionS.getUser()
    this.whoIs=userData.user.name
    console.log(userData)
    if(userData.user.designation=="Head"){
      this.Hide=false

    }
  }
  selectedYear=''
  yearArray=['','2012','2013','2014','2015']
  selectedMonth=''
  monthArray=['','Jan','Jun']
  getPhysGCSEQuestions(){
    this.showTable=true
    var user=this.sessionS.getUser();
    this.http.get("/phys?token="+user.token+"&year="+this.selectedYear+"&month="+this.selectedMonth).subscribe(
      data=>{
        this.physArray=data
        console.log('checking success')
        console.log(typeof this.physArray)
      },
      error=>{
        if(error.status==401){
          this.sessionS.deleteUser();
          this.router.navigateByUrl("/login")
        }
      }
    )
  }
  getDashboard(){
    this.router.navigateByUrl("/dashboard")
  }
  //
  Serial_number=11;
  year='2016';
  month='Jan';
  question_number='4';
  question='Hello2'
  marks=5;

 

  postQuestions(){
    var user=this.sessionS.getUser();
    console.log('front end posting')
    this.http.post("/phys",{
      token:user.token,
      Serial_number:+this.Serial_number,
      year: this.year,
      month:this.month, 
      question_number:this.question_number,
      question:this.question,
      marks:+this.marks}).subscribe(
      (data)=>{
        console.log(data)
        //window.location.href="" this line include if you want to go to dashboard
      },
      (error)=>{

      }
    )
  }
  id="";
  questionObj={
    token:'',
    Serial_number:11,
    year:'2016',
    month:'Jan',
    question_number:'4',
    question:'Hello3',
    marks:5
  }
  putQuestions(){
    var user=this.sessionS.getUser();
    this.questionObj.token=user.token;
    console.log('front end put')
    console.log(this.id)
    this.http.put("/phys/"+this.id,this.questionObj).subscribe(
      (data)=>{
        console.log(data)

        //window.location.href="" this line include if you want to go to dashboard
      },
      (error)=>{

      }
    )


  }


}
