import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SessionService {

  constructor() { }

  getUser(){
    var user=localStorage.getItem("user")
    if(user){
      return(JSON.parse(user))
    } else{
      return {};
    }
  }
  setUser(user){
    localStorage.setItem("user",JSON.stringify(user))
  }
  deleteUser(){
    localStorage.removeItem("user")
  }
}
