  ngOnInit(){

  }
  getUzers(){

    this.showTableUser=true
    var user=this.sessionS.getUser();
    this.http.get(`/users?token=${user.token}&username=${this.queryUsername}`).subscribe(
    //this.http.get("/users?token="+user.token).subscribe(
      data=>{
        console.log(data)
        this.allUsers=data
        for (const element of this.allUsers) {
          console.log('inside loop')
          console.log(element);
        }
      },
      error=>{
        if(error.status==401){
          this.sessionS.deleteUser();
          this.router.navigateByUrl("/login")
        }
      }
    )
  }
