import { Component, OnInit } from '@angular/core';
import { SessionService } from '../session.service';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  constructor(private sessionS:SessionService,private http:HttpClient, private router:Router) { }
  Hide=true;
  showTable=false;
  showTableUser=false;

  physArray: any
  allUsers: any
  whoIs="check"
  ngOnInit() {
    var userData=this.sessionS.getUser()
    this.whoIs=userData.user.name
    console.log(userData)
    if(userData.user.designation=="Head"){
      this.Hide=false

    }

  }

  logout(){
    var userData=this.sessionS.getUser()
    console.log('token:::',userData.token)
    console.log('userInfo:::',userData.user)
    this.http.post("/auth/logout",{token:userData.token}).subscribe(
      (data)=>{
        this.sessionS.deleteUser()
        this.router.navigateByUrl("/login")
      },
      (error)=>{
        console.log('error')
      }
    )

    }
  getPhys(){
    this.showTable=true
    var user=this.sessionS.getUser();
    this.http.get("/phys?token="+user.token).subscribe(
      data=>{
        this.physArray=data
        console.log('checking success')
        console.log(typeof this.physArray)
      },
      error=>{
        if(error.status==401){
          this.sessionS.deleteUser();
          this.router.navigateByUrl("/login")
        }
      }
    )
  }

  getPhysgcsePage(){
    this.router.navigateByUrl("/physicsGCSE")
  }
  getChemgcsePage(){
    this.router.navigateByUrl("/chemistryGCSE")
  }
  getUsersPage(){
    this.router.navigateByUrl("/usersDataControl")
  }

 

  getUzers(){
    this.showTableUser=true
    var user=this.sessionS.getUser();
    this.http.get("/users?token="+user.token).subscribe(
      data=>{
        console.log(data)
        this.allUsers=data
        for (const element of this.allUsers) {
          console.log('inside loop')
          console.log(element);
        }
      },
      error=>{
        if(error.status==401){
          this.sessionS.deleteUser();
          this.router.navigateByUrl("/login")
        }
      }
    )
  }

  hideQuestions(){
    this.showTable=false;
  }
}
